import { Pipe, PipeTransform } from "@angular/core";

@Pipe({name: 'ezElementPosition', pure: true})
export class EzElementPositionPipe implements PipeTransform {
   transform(value: HTMLElement, xLerp: number = 1, yLerp: number = 1) {
       if (value != null) {
           const boundingRect = value.getBoundingClientRect();
           return {
               x: boundingRect.left + xLerp * boundingRect.width,
               y: boundingRect.top + yLerp * boundingRect.height,
           };
       } else {
           return null;
       }
   }
}