import { Pipe, PipeTransform } from "@angular/core";
import { CountriesList, CoutryConvertType } from "./ez-pipes-angular.type";


@Pipe({name: 'ezCurrency'})
export class EzCurrencyPipe implements PipeTransform {

    transform(value: any, format: CoutryConvertType) {
        if(['', null, undefined, NaN].includes(value)) return value;
        const cur = CountriesList;
        if(format === 'currencycode2Symbol') {
            return cur[value] ? cur[value].currency.symbol : value;
        } else if (format === 'currencycode2country') {
            return cur[value] ? cur[value].country.name : value;
        } else {
            return value;
        }
    }

}