import { Pipe, PipeTransform } from "@angular/core";
import { MathOperationType } from "./ez-pipes-angular.type";

@Pipe({name: 'ezMath'})
export class EzMathPipe implements PipeTransform {

    transform(value: number, method: MathOperationType, withNumber?: number): string | number {
        if(!value) return '';
        if(method === 'sqrt'){
            return Math.sqrt(value);
        }else if(method === 'pow'){
            return Math.pow(value, withNumber ?? value);
        } else if(method === 'round') {
            return Math.round(value);
        } else if(method === 'ceil') {
            return Math.ceil(value);
        } else if(method === 'floor'){
            return Math.floor(value);
        } else if(method === 'trunc') {
            return Math.trunc(value);
        } else if(method === 'abs') {
            return Math.abs(value);
        } else if(method === 'fahrenheit2celsius') {
            return ((5 / 9) * (value - 32))+' ℃';
        } else if(method === 'celsius2fahrenheit') {
            return ((value - 32) * 5 / 9) + ' °F';
        }
        return value;
    }
}