// number format
export type NumberFormats = 'abbreviate' | 'dec2bin' | 'dec2hex' | '1.0-0' | '1.1-1' | '1.2-2' | '1.3-3' 
| 'comma-separate' | 'bin2hex';

// text format
export type TextFormats = 'lowercase' | 'uppercase' | 'camelcase' | 'capitalize' | 'reverse';

// math operation types

export type MathOperationType = 'sqrt' | 'pow' | 'round' | 'ceil' | 'floor' | 'trunc' | 'abs' 
| 'fahrenheit2celsius' | 'celsius2fahrenheit';

// Country code base
export type CoutryConvertType = 'currencycode2Symbol' | 'currencycode2country';

export interface Countries {
    name: string;
    wikipedia: string;
    currency: CountriesCurrency;
    country: CountriesCountry;
}

