import { Pipe, PipeTransform } from "@angular/core";
import { TextFormats } from "./ez-pipes-angular.type";

@Pipe({name: 'ezText'})
export class EzTextPipe implements PipeTransform {

    transform(value: string, format: TextFormats) {
        if([null, '', undefined, NaN].includes(value)) return value; // terminate immediately
        if(format === 'lowercase') {
            return value.toLowerCase();
        } else if(format === 'uppercase') {
            return value.toUpperCase();
        } else if (format === 'camelcase') {
            return value.replace(/(?:^\w|[A-Z]|\b\w)/g, function(word, index) {
                return index === 0 ? word.toLowerCase() : word.toUpperCase();
              }).replace(/\s+/g, '');
        } else if(format === 'capitalize') {
            return value.toLowerCase().split(' ').map(s => s.charAt(0).toUpperCase() + s.substring(1)).join(' ');
        } else if (format === 'reverse') {
            return value.split("").reverse().join("");
        }
        return value;
    }
}